'use client';

import React, { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardHeader, CardTitle, CardContent, CardDescription, CardFooter } from '@/components/ui/card';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { PlusCircle, Edit2, Trash2, Archive, DollarSign, Loader2 } from 'lucide-react';
import { format } from "date-fns";
import { es } from "date-fns/locale";
import { Popover, PopoverTrigger, PopoverContent } from '@/components/ui/popover';
import { CalendarIcon } from 'lucide-react';
import { Calendar } from '@/components/ui/calendar';
import { formatCurrencyCLP } from '@/lib/utils';
import { getInventoryItems, createInventoryItem, updateInventoryItem, deleteInventoryItem, InventoryItem } from '@/lib/inventory';

export default function InventoryPage() {
  const [inventoryItems, setInventoryItems] = useState<InventoryItem[]>([]);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [currentItem, setCurrentItem] = useState<Partial<InventoryItem>>({
    name: '',
    quantity: 0,
    purchasePrice: 0,
    description: null,
    sku: null,
    supplier: null,
    lastRestocked: null,
  });
  const [editingId, setEditingId] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // Cargar los artículos del inventario
  useEffect(() => {
    const loadInventory = async () => {
      try {
        const items = await getInventoryItems();
        setInventoryItems(items);
      } catch (error) {
        console.error('Error al cargar el inventario:', error);
      } finally {
        setIsLoading(false);
      }
    };

    loadInventory();
  }, []);

  const handleAddOrUpdateItem = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      const formData = new FormData(e.currentTarget);
      const name = formData.get('name') as string;
      const quantity = formData.get('quantity') ? parseInt(formData.get('quantity') as string) : 0;
      const purchasePrice = formData.get('purchasePrice') ? Math.round(parseFloat(formData.get('purchasePrice') as string)) : 0;
      const description = (formData.get('description') as string) || null;
      const sku = (formData.get('sku') as string) || null;
      const supplier = (formData.get('supplier') as string) || null;
      
      console.log('Form data:', {
        name,
        quantity,
        purchasePrice,
        description,
        sku,
        supplier,
        lastRestocked: currentItem.lastRestocked
      });
      
      // Validar campos requeridos
      if (!name.trim()) {
        throw new Error('El nombre del artículo es requerido');
      }
      
      if (isNaN(quantity) || quantity < 0) {
        throw new Error('La cantidad debe ser un número positivo');
      }
      
      if (isNaN(purchasePrice) || purchasePrice < 0) {
        throw new Error('El precio de compra debe ser un número positivo');
      }
      
      const itemData = {
        name: name.trim(),
        quantity,
        purchasePrice,
        description: description?.trim() || null,
        sku: sku?.trim() || null,
        supplier: supplier?.trim() || null,
        lastRestocked: currentItem.lastRestocked || null,
      };

      if (editingId) {
        console.log('Updating item with ID:', editingId);
        const updatedItem = await updateInventoryItem({ 
          ...itemData,
          id: editingId
        });
        
        // Actualizar el estado local con el artículo actualizado
        setInventoryItems(prevItems => 
          prevItems.map(item => item.id === editingId ? { ...updatedItem } : item)
        );
      } else {
        console.log('Creating new item');
        const newItem = await createInventoryItem(itemData);
        setInventoryItems(prevItems => [...prevItems, newItem]);
      }
      
      // Cerrar el diálogo y limpiar el formulario
      setIsDialogOpen(false);
      setCurrentItem({
        name: '',
        quantity: 0,
        purchasePrice: 0,
        description: null,
        sku: null,
        supplier: null,
        lastRestocked: null,
      });
      setEditingId(null);
    } catch (error) {
      console.error('Error al guardar el artículo:', error);
      
      let errorMessage = 'Ocurrió un error al guardar el artículo. Por favor, inténtalo de nuevo.';
      
      if (error instanceof Error) {
        errorMessage = error.message || errorMessage;
      } else if (typeof error === 'string') {
        errorMessage = error;
      }
      
      // Mostrar el mensaje de error en la interfaz
      alert(errorMessage);
      
      // No cerrar el diálogo si hay un error de validación
      if (error instanceof Error && error.message.includes('requerido') || 
          error instanceof Error && error.message.includes('debe ser')) {
        return; // Mantener el diálogo abierto
      }
      
      // Cerrar el diálogo solo si no es un error de validación
      setIsDialogOpen(false);
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleEdit = (item: InventoryItem) => {
    setCurrentItem(item);
    setEditingId(item.id);
    setIsDialogOpen(true);
  };

  const handleDelete = async (id: string) => {
    if (confirm('¿Estás seguro de que deseas eliminar este artículo?')) {
      try {
        await deleteInventoryItem(id);
        setInventoryItems(inventoryItems.filter(item => item.id !== id));
      } catch (error) {
        console.error('Error al eliminar el artículo:', error);
      }
    }
  };

  const totalInventoryValue = inventoryItems.reduce((sum, item) => sum + (item.quantity * item.purchasePrice), 0);
  const totalStockUnits = inventoryItems.reduce((sum, item) => sum + item.quantity, 0);

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-headline font-semibold">Gestión de Inventario (Componentes y Materias Primas)</h1>
        <Dialog open={isDialogOpen} onOpenChange={(isOpen) => {
          setIsDialogOpen(isOpen);
          if (!isOpen) {
            setCurrentItem({});
            setEditingId(null);
          }
        }}>
          <DialogTrigger asChild>
            <Button>
              <PlusCircle className="mr-2 h-4 w-4" /> Añadir Componente/Material
            </Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-lg max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>{editingId ? 'Editar' : 'Añadir Nuevo'} Componente/Material</DialogTitle>
              <DialogDescription>
                Completa los detalles del componente o materia prima.
              </DialogDescription>
            </DialogHeader>
            <form onSubmit={handleAddOrUpdateItem} className="space-y-4">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <Label htmlFor="name">Nombre del Componente/Material</Label>
                  <Input 
                  id="name" 
                  name="name" 
                  value={currentItem.name || ''} 
                  onChange={(e) => setCurrentItem({...currentItem, name: e.target.value})} 
                  required 
                />
                </div>
                <div>
                  <Label htmlFor="sku">SKU (Opcional)</Label>
                  <Input 
                  id="sku" 
                  name="sku" 
                  value={currentItem.sku || ''} 
                  onChange={(e) => setCurrentItem({...currentItem, sku: e.target.value || null})} 
                />
                </div>
                <div>
                  <Label htmlFor="quantity">Cantidad</Label>
                  <Input 
                  id="quantity" 
                  name="quantity" 
                  type="number" 
                  step="1" 
                  value={currentItem.quantity || 0} 
                  onChange={(e) => setCurrentItem({...currentItem, quantity: parseInt(e.target.value) || 0})} 
                  required 
                />
                </div>
                <div>
                  <Label htmlFor="purchasePrice">Precio de Compra (Unidad, CLP)</Label>
                  <Input 
                  id="purchasePrice" 
                  name="purchasePrice" 
                  type="number" 
                  step="1" 
                  value={currentItem.purchasePrice || 0} 
                  onChange={(e) => setCurrentItem({...currentItem, purchasePrice: parseFloat(e.target.value) || 0})} 
                  required 
                />
                </div>
                <div>
                  <Label htmlFor="supplier">Proveedor (Opcional)</Label>
                  <Input 
                  id="supplier" 
                  name="supplier" 
                  value={currentItem.supplier || ''} 
                  onChange={(e) => setCurrentItem({...currentItem, supplier: e.target.value || null})} 
                />
                </div>
                <div className="md:col-span-2">
                  <Label htmlFor="lastRestocked">Última Reposición (Opcional)</Label>
                  <Popover>
                    <PopoverTrigger asChild>
                      <Button
                        variant={"outline"}
                        className="w-full justify-start text-left font-normal"
                      >
                        <CalendarIcon className="mr-2 h-4 w-4" />
                        {currentItem.lastRestocked ? format(new Date(currentItem.lastRestocked), "PPP", { locale: es }) : <span>Selecciona una fecha</span>}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent className="w-auto p-0">
                      <Calendar
                        mode="single"
                        selected={currentItem.lastRestocked ? new Date(currentItem.lastRestocked) : undefined}
                        onSelect={(date) => setCurrentItem((prev: Partial<InventoryItem>) => ({ ...prev, lastRestocked: date || null }))}
                      />
                    </PopoverContent>
                  </Popover>
                </div>
              </div>
              <div>
                <Label htmlFor="description">Descripción (Opcional)</Label>
                <Textarea 
                  id="description" 
                  name="description" 
                  value={currentItem.description || ''} 
                  onChange={(e) => setCurrentItem({...currentItem, description: e.target.value || null})} 
                />
              </div>
              <DialogFooter>
                <Button type="button" variant="outline" onClick={() => setIsDialogOpen(false)}>Cancelar</Button>
                <Button type="submit" disabled={isSubmitting}>
                  {isSubmitting ? (
                    <>
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                      {editingId ? 'Actualizando...' : 'Guardando...'}
                    </>
                  ) : (
                    <>{editingId ? 'Actualizar' : 'Guardar'} Artículo</>
                  )}
                </Button>
              </DialogFooter>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid gap-4 md:grid-cols-2">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Unidades Totales en Stock</CardTitle>
            <Archive className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{totalStockUnits.toLocaleString('es-CL')}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Valor Total del Inventario</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrencyCLP(totalInventoryValue)}</div>
            <p className="text-xs text-muted-foreground">Basado en precio de compra.</p>
          </CardContent>
        </Card>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Lista de Componentes y Materias Primas</CardTitle>
          <CardDescription>Todos los artículos base en tu inventario.</CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Nombre</TableHead>
                <TableHead>SKU</TableHead>
                <TableHead className="text-right">Cantidad</TableHead>
                <TableHead className="text-right">Precio Compra</TableHead>
                <TableHead>Proveedor</TableHead>
                <TableHead>Últ. Reposición</TableHead>
                <TableHead className="text-right">Acciones</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {inventoryItems.length > 0 ? inventoryItems.map((item) => (
                <TableRow key={item.id}>
                  <TableCell className="font-medium">{item.name}</TableCell>
                  <TableCell>{item.sku || '-'}</TableCell>
                  <TableCell className="text-right">{item.quantity.toLocaleString('es-CL')}</TableCell>
                  <TableCell className="text-right">{formatCurrencyCLP(item.purchasePrice)}</TableCell>
                  <TableCell>{item.supplier || '-'}</TableCell>
                  <TableCell>{item.lastRestocked ? format(item.lastRestocked, "dd/MM/yy") : '-'}</TableCell>
                  <TableCell className="text-right space-x-1">
                    <Button variant="ghost" size="icon" onClick={() => handleEdit(item)}>
                      <Edit2 className="h-4 w-4" />
                    </Button>
                    <Button variant="ghost" size="icon" onClick={() => handleDelete(item.id)}>
                      <Trash2 className="h-4 w-4 text-destructive" />
                    </Button>
                  </TableCell>
                </TableRow>
              )) : (
                <TableRow>
                  <TableCell colSpan={7} className="text-center">No hay artículos en el inventario.</TableCell>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}